import React from 'react';
import GatsbyImage from 'gatsby-image';
import styled, { css } from 'styled-components';
import { graphql } from 'gatsby';

import SEO from '../components/Seo';

const headerStyles = ({ theme }) => css`
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 3.5rem;
  color: ${theme.colors.p700};
`;

const Header = styled('h1')`
  ${headerStyles}
`;

const titleStyles = ({ theme }) => css`
  margin-top: 0;
  color: ${theme.colors.p400};
`;

const Title = styled('h2')`
  ${titleStyles}
`;

const imageStyles = css`
  left: 0;
  top: 0;
  margin-bottom: 20px;
`;

const Image = styled(GatsbyImage)`
  ${imageStyles}
`;

const textStyles = ({ theme }) => css`
  color: ${theme.colors.textColor};
`;

const Text = styled('p')`
  ${textStyles}
`;

const AboutPage = ({ location, data }) => {
  const { avatar, site } = data;
  const { author } = site.siteMetadata;

  return (
    <div>
      <SEO title="About" />
      <Header>I'm Guy</Header>
      <Title>A software engineer living in Berlin</Title>
      <Image fluid={avatar.childImageSharp.fluid} alt={author} />
      <Text>
        I began my journey as a software engineer nearly 7 years ago, I've
        worked in various industries and collaborated with talented people to
        create products for both business and consumer use. I'm quietly
        confident, naturally curious, and perpetually working on improving my
        skills one problem at a time.
      </Text>
    </div>
  );
};
export default AboutPage;

export const pageQuery = graphql`
  query allImages {
    avatar: file(absolutePath: { regex: "/guy-about.jpg/" }) {
      childImageSharp {
        fluid(maxWidth: 1000, maxHeight: 300, cropFocus: CENTER) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    site {
      siteMetadata {
        author
        social {
          twitter
          github
          linkedin
        }
      }
    }
  }
`;
